.InputTipValue{
    width: 379px;
    margin: 45px auto 0 auto;
    font-family: "Space Mono";
}

.bill-input {
  color: #5E7A7D;
  font-size: 24px;
}

.bill-label{
    display: block;
    position: relative;
    color: #5E7A7D;
    font-size: 16px;
    margin-bottom: 6px;
}

.bill-value{
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 48px;
    background-color: #F3F9FA ;
    border: none;
    border-radius: 5px;
    text-align: right;
    padding-right: 17px; 
    font-family: "Space Mono";
    font-size: inherit;
    color: #00474B;
    appearance: none;
}

.bill-value::placeholder{
    color: #5E7A7D;
    font-size: 24px;
    font-family: "Space Mono";
    padding-right: 17px; 
}

.bill-value:focus{
    border: 2px solid#26C2AE;
    outline: none;
    transition: all 0.05s;
}


.bill-label::after {
    content: url(/public/assets/images/icon-dollar.svg);
    z-index: 10;
    position: absolute;
    top: 46px;
    left: 20px;
}



@media only screen and (max-width : 375px) {
    .InputTipValue{
        width: 100%;
        margin: 32px auto 0 auto;
    }


      

}